.theme--dark.v-navigation-drawer:not(.v-navigation-drawer--floating) .v-navigation-drawer__border{background-color:hsla(0,0%,100%,.12)}     
.theme--dark.v-navigation-drawer .v-divider{border-color:hsla(0,0%,100%,.12)}     
.v-navigation-drawer{-webkit-transition:none;transition:none;display:block;left:0;max-width:100%;overflow-y:auto;overflow-x:hidden;pointer-events:auto;top:0;will-change:transform;-webkit-overflow-scrolling:touch}     
.v-navigation-drawer[data-booted=true]{-webkit-transition:.2s cubic-bezier(.4,0,.2,1);transition:.2s cubic-bezier(.4,0,.2,1);transition-property:width,-webkit-transform;-webkit-transition-property:width,-webkit-transform;transition-property:transform,width;transition-property:transform,width,-webkit-transform}     
.v-navigation-drawer__border{position:absolute;right:0;top:0;height:100%;width:1px}     
.v-navigation-drawer.v-navigation-drawer--right:after{left:0;right:auto}     
.v-navigation-drawer--right{left:auto;right:0}     
